import './portada.css';

import {
  Card,
  Container,
  Grid,
  Header,
  Icon,
  Image,
  Label,
  Pagination,
  Popup,
  Segment
} from "semantic-ui-react";
import {
  GetErabiltzaileak,
  GetErabiltzailearenKlipak,
  GetZuzenekoak,
} from "./api.js";
import React, { useEffect, useState } from "react";

import { Link } from "react-router-dom";
import ZuzenekoaCard from "./ZuzenekoaCard";
import moment from "moment";
import twitzlariak from "./twitzlariak.json";

const Portada = (props) => {
  const [klipak, setKlipak] = useState([]);
  const [users, setUsers] = useState([]);
  const [paginationKlipak, setPaginationKlipak] = useState(klipak.slice(0, 12));
  const [paginationOrria, setPaginationOrria] = useState(1);
  const [zuzenekoak, setZuzenekoak] = useState([]);
  const getZuzenekoak = async () =>
    setZuzenekoak(await GetZuzenekoak(twitzlariList));
  useEffect(() => {
    getZuzenekoak();
  }, []);
  const twitzlariList = twitzlariak.twitzlariak;
  let gaur = new Date();
  let atzeraData = new Date();
  atzeraData.setDate(gaur.getDate() - 20);
  function dynamicSort(property) {
    var sortOrder = 1;
    if (property[0] === "-") {
      sortOrder = -1;
      property = property.substr(1);
    }
    return function (a, b) {
      /* next line works with strings and numbers,
       * and you may want to customize it to your needs
       */
      var result =
        a[property] < b[property] ? -1 : a[property] > b[property] ? 1 : 0;
      return result * sortOrder;
    };
  }

  const getErabiltzailearenKlipak = async (user_id) => {
    return await GetErabiltzailearenKlipak(user_id);
  };
  const getErabiltzaileak = async (users) => {
    return await GetErabiltzaileak(users);
  };

  useEffect(() => {
    getErabiltzaileak(twitzlariList).then((response) => {
      setUsers(response);
    });
  }, []);

  useEffect(() => {
    setPaginationKlipak(
      klipak
        .sort(dynamicSort("-created_at"))
        .slice((paginationOrria - 1) * 12, (paginationOrria - 1) * 12 + 12)
    );
  }, [klipak, paginationOrria]);

  useEffect(() => {
    if (users.length > 0) {
      users.map((user) => {
        getErabiltzailearenKlipak(user.id).then((response) => {
          response.map((klipa) => {
            setKlipak((prevState) => [...prevState, klipa]);
          });
        });
      });
    }
  }, [users]);

  const handlePaginationChange = (e, { activePage }) =>
    setPaginationOrria(activePage);

  return (
    <Container>
      <h1>Hasierako orria</h1>
      <div className="pulsating-circle"></div>
      <h2 className="pulsating-circle">Orain zuzenean</h2>
      {zuzenekoak.length > 0 ? (
        <Card.Group itemsPerRow={4} stackable>
          {zuzenekoak.map((erabiltzailea, index) => (
            <ZuzenekoaCard erabiltzailea={erabiltzailea} />
            ))}
            </Card.Group>
        ) : (
          <Segment placeholder>
            <Header icon>
              <Icon name="history" />
              Orain momentuan ez dago inor zuzenean.
            </Header>
          </Segment>
        )}
      <hr />
      <h2>Azken 40 egunetako klipak</h2>
      <Card.Group itemsPerRow={4} stackable>
        {paginationKlipak.map((clip) => (
          // <Card>
          //   <Image
          //     src={clip.thumbnail_url
          //       .replace("%{width}", 440)
          //       .replace("%{height}", 228)}
          //   />
          //   <Card.Content>
          //     <a href={clip.url} rel="noreferrer" target="_blank">
          //       <Card.Header>{clip.title}</Card.Header>
          //     </a>
          //     <Card.Description as="a">
          //       {moment(clip.created_at).format("YYYY/MM/DD HH:MM")}
          //     </Card.Description>
          //   </Card.Content>
          // </Card>
          <Card>
            <Card.Content>
              <Label attached="top left" color="violet">
                {clip.broadcaster_name}
              </Label>
              <Card.Header>{clip.title}</Card.Header>
            </Card.Content>
            <a href={clip.url} target="_blank" rel="noreferrer">
              <Image
                src={clip.thumbnail_url
                  .replace("%{width}", 440)
                  .replace("%{height}", 228)}
              />
            </a>
            <Card.Content extra>
              <Grid divided="vertically">
                <Grid.Row columns={2}>
                  <Grid.Column>
                    <div>
                      <Icon name="eye" />
                      {clip.view_count} ikustaldi
                    </div>
                    <div>
                      <Icon name="clock" />
                      {moment(clip.created_at).format("YYYY/MM/DD")}
                    </div>
                  </Grid.Column>
                  <Grid.Column>
                    <a
                      type="button"
                      role="button"
                      title="Share on twitter"
                      target={"_blank"}
                      rel="noreferrer"
                      href={
                        "https://twitter.com/intent/tweet?url=" +
                        "[" +
                        clip.broadcaster_name +
                        "]" +
                        "%0A" +
                        clip.title +
                        "%0A%0A" +
                        clip.url
                      }
                    >
                      <Icon name="twitter" color="blue" size="big" />
                    </a>
                    <Popup
                      content="Arbelean kopiatu da zure sareetan partekatzeko!"
                      hideOnScroll
                      basic
                      inverted
                      position="top center"
                      on="click"
                      trigger={
                        <Icon
                          name="copy"
                          size="big"
                          onClick={() => {
                            navigator.clipboard.writeText(
                              "[" +
                                clip.broadcaster_name +
                                "]" +
                                "\n" +
                                clip.title +
                                "\n\n" +
                                clip.url
                            );
                          }}
                        />
                      }
                    />
                  </Grid.Column>
                </Grid.Row>
              </Grid>
            </Card.Content>
            {console.log(clip)}
          </Card>
          // <Card
          //   image={clip.thumbnail_url
          //     .replace("%{width}", 440)
          //     .replace("%{height}", 228)}
          //   description={clip.broadcaster_name}
          //   header={clip.title}
          // >
          //   {/* <Label color="red" ribbon="right">
          //     Zuzenean
          //   </Label> */}
          // </Card>
        ))}
      </Card.Group>
      <Segment>

      <Pagination
        boundaryRange={1}
        defaultActivePage={1}
        showEllipsis={true}
        activePage={paginationOrria}
        onPageChange={handlePaginationChange}
        totalPages={Math.round(klipak.length / 12)}
        />
        </Segment>
    </Container>
  );
};

export default Portada;
